
<script>
import list from './v.s.list.vue'
export default {
    name:"LIST-WRAPPABLE",
    extends:list,
    computed:{}
}
</script>

<style lang="less">
/**
    --list-gap
    --list-gap-column
    --list-gap-row
    --list-item-basis
    --list-flow
*/
.LIST-WRAPPABLE
{
    &.LIST.ROOT
    {
        --list-wrap:wrap;
        --list-gap:1em;
        --list-direction:row;
        padding:var(--list-gap,0px);
        box-sizing:border-box;
        display: inline-grid;
        align-items: center;
        justify-items: center;
        grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
        justify-content: var(--list-justfied,center);
        grid-column-gap: var(--list-gap-column,var(--list-gap,0px));
        grid-row-gap: var(--list-gap-row,var(--list-gap,0px));
        width:100%;
        height: 100%;
    }}
</style>



